import { connect } from "react-redux";
import React, { Component } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
// import Button from '@mui/material/Button';
// import TextField from '@mui/material/TextField';
import Select from "react-select";

import { toyService } from "../services/toyService.js";
const options = [
  { value: "On wheels", label: "On wheels" },
  { value: "Art", label: "Art" },
  { value: "Box game", label: "Box game" },
  { value: "Baby", label: "Baby" },
  { value: "Doll", label: "Doll" },
  { value: "Puzzle", label: "Puzzle" },
  { value: "Outdoor", label: "Outdoor" },
];

export class _ToyAdd extends React.Component {
  state = {
    toy: {
      name: "",
      price: 0,
      inStock: true,
      label: [],
      reviews: [],
      createdAt: Date.now(),
    },
  };

  onAddToy = (ev) => {
    ev.preventDefault();

    const { toy } = this.state;
    toyService
      .save({ ...toy })
      .then((savedToy) => {
        const action = { type: "ADD_TOY", toy: savedToy };
        this.props.dispatch(action);
        // showSuccessMsg("Toy added");
      })
      .catch((err) => {
        // showErrorMsg("Cannot add toy");
      });
  };

  onHandleChange = ({ target }) => {
    const field = target.name;
    const value = target.value;
    // console.log("field", field, "value", value);
    this.setState((prevState) => ({
      toy: { ...prevState.toy, [field]: value },
    }));
  };

  onHandleChangeMulti = (options) => {
    console.log("options", options);
    this.setState((prevState) => ({
      toy: {
        ...prevState.toy,
        label: [...options.map((option) => option.value)],
      },
    }));
  };

  render() {
    const { toy } = this.state;
    return (
      <section className="toy-app">
        <div className="toy-add-container">
          <h2 className="margin-top">Add new toy</h2>
          <Formik
            initialValues={{ name: "", price: "" }}
            validate={(values) => {
              const errors = {};
              console.log("values.name:", values.name);
              if (!values.name) {
                errors.name = "Required";
              }

              return errors;
            }}
            onSubmit={(values, { setSubmitting }) => {
              setTimeout(() => {
                alert(JSON.stringify(values, null, 2));
                setSubmitting(false);
              }, 400);
            }}
          >
            {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              isSubmitting,
              /* and other goodies */
            }) => (
              <form className="toy-form" onSubmit={this.onAddToy}>
                <div>
                  <label htmlFor="txt">Enter new toy:</label>
                  <input
                    placeholder="Enter toy"
                    value={this.state.toy.name}
                    name="name"
                    onChange={this.onHandleChange}
                  ></input>
                </div>
                <div>
                  <label htmlFor="price">Price:</label>
                  <input
                    value={this.state.toy.price}
                    type="number"
                    name="price"
                    min="0"
                    onChange={this.onHandleChange}
                    placeholder="Toy's price"
                  ></input>
                </div>
                <div>
                  <Select
                    // defaultValue={[colourOptions[2], colourOptions[3]]}
                    isMulti
                    name="label"
                    options={options}
                    className="basic-multi-select"
                    classNamePrefix="select"
                    onChange={this.onHandleChangeMulti}
                  />
                </div>
                <div className="add-toy-btn-container">
                  <button className="add-toy-btn">Add Toy</button>
                </div>
              </form>
            )}
          </Formik>
          {/* <form className="toy-form" onSubmit={this.onAddToy}>
            <div>
              <label htmlFor="txt">Enter new toy:</label>
              <input
                placeholder="Enter toy"
                value={this.state.toy.name}
                name="name"
                onChange={this.onHandleChange}
              ></input>
            </div>
            <div>
              <label htmlFor="price">Price:</label>
              <input
                value={this.state.toy.price}
                type="number"
                name="price"
                min="0"
                onChange={this.onHandleChange}
                placeholder="Toy's price"
              ></input>
            </div>
            <div>
              <Select 
                // defaultValue={[colourOptions[2], colourOptions[3]]}
                isMulti
                name="label"
                options={options}
                className="basic-multi-select"
                classNamePrefix="select"
                onChange={this.onHandleChangeMulti}
              />
            </div>
            <div className="add-toy-btn-container">
              <button className="add-toy-btn">Add Toy</button>
            </div>
          </form> */}
        </div>
      </section>
    );
  }
}
function mapStateToProps({ toyModule }) {
  return {
    toys: toyModule.toys,
  };
}

export const ToyAdd = connect(mapStateToProps)(_ToyAdd);
