import { connect } from "react-redux";
import React from "react";
import Select from "react-select";

import { toyService } from "../services/toyService";
import {ToyDetails} from './ToyDetails'
const options = [
    { value: "On wheels", label: "On wheels" },
    { value: "Art", label: "Art" },
    { value: "Box game", label: "Box game" },
    { value: "Baby", label: "Baby" },
    { value: "Doll", label: "Doll" },
    { value: "Puzzle", label: "Puzzle" },
    { value: "Outdoor", label: "Outdoor" },
  ];
  
class _ToyEdit extends React.Component {
  state = {
    toy: null,
  };
  inputRef = React.createRef();

  componentDidMount() {
    const { toyId } = this.props.match.params;
    toyService
      .getById(toyId)
      .then((toy) => {
        this.lastSavedState = { ...toy };
        return toy;
      })
      .then((toy) => this.setState({ toy }));
  }

  handleChange = ({ target }) => {
    let { name, value } = target;
    if (value === "no") value = (!value === "no");
    else if (value === "yes") value = (value === "yes");
    else if (value === "chose") value = this.state.toy.isStock;
    this.setState((prevState) => ({
      toy: { ...prevState.toy, [name]: value },
    }));
  };


  onSave = (ev) => {
     ev.preventDefault();
    toyService
      .save(this.state.toy)
      .then(() => this.props.history.push("/"));
  };

  onGoBack = () => {
    const { toy } = this.state;
    // the statement below checks if the user saved his changes.
    if (
      this.lastSavedState.name !== toy.name &&
      this.lastSavedState.price !== toy.price
    ) {
    } else this.props.history.push("/");
  };

  get creationDate() {
    const date = new Date(this.state.toy.createdAt).toDateString();
    const time = new Date(this.state.toy.createdAt).toLocaleTimeString();
    return date + ', ' + time;
}

onHandleChangeMulti = (options) => {
    console.log("options", options);
    this.setState((prevState) => ({
        toy: { ...prevState.toy, label: [...options.map(option=>option.value)] },
    }));
  };

  render() {
    const { toy } = this.state;
    if (!toy) return "No such toy under given id.";
    return (
      <section className="edit-section main-container">
        <h2 className="margin-top">Edit {toy.name}</h2>
        <p>Created at <span>{this.creationDate}</span></p>
        <form className="form-edit" onSubmit={this.onSave}>
        <label>New Name:</label>
          <input
            type="text"
            ref={this.inputRef}
            name="name"
            value={toy.name}
            onChange={this.handleChange}
            placeholder="Enter toy name..."
            required
          />
          <label>Price:</label>
          <input
            type="number"
            name="price"
            value={toy.price}
            onChange={this.handleChange}
            placeholder="toy new price?"
            required
          />
          <div>

          <label>In Stock?</label>
          <select name="inStock" onChange={this.handleChange}>
            <option value="chose">Chose</option>
            <option value="yes">Yes</option>
            <option value="no">No</option>
          </select>
          </div>
      
          <label>label</label>
          <Select 
                isMulti
                name="label"
                options={options}
                className="basic-multi-select"
                classNamePrefix="select"
                onChange={this.onHandleChangeMulti}
              />
          <button>Save</button>
        </form>
        <button onClick={this.onGoBack} title="go back">
          <span>Back</span>
        </button>
        {/* <ToyDetails/> */}
      </section>
    );
  }
}

function mapStateToProps({ toyModule }) {
  return {
    toys: toyModule.toys,
  };
}

export const ToyEdit = connect(mapStateToProps)(_ToyEdit);
