import { ToyPreview } from "../cmps/ToyPreview.jsx";

export function ToyList({ toys , onRemoveToy}) {

console.log('toys', toys);
  if (!toys) return <p>Loading...</p>;
  if (!toys.length) return <p>No Todos Yet!</p>;

  return toys.map((toy, idx) => {
    return <ToyPreview key={idx} toy={toy} onRemoveToy={onRemoveToy} />;
  });
}
