import { connect } from "react-redux";
import React from "react";
import { Link } from "react-router-dom";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

import { Bar } from "react-chartjs-2";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

class _ToyChart extends React.Component {
  state = {};

  componentDidMount() {
    //TODO : add toys from query
    const map = this.mapChart();
    this.setState({ map });
  }

  mapChart = () => {
    const mapObj = {};
    this.props.toys.map((toy) => {
      return toy.label.map((label) => {
        const toysPerLable = this.props.toys.filter((toy) => {
          return toy.label.includes(label);
        });
        return (mapObj[label] = mapObj[label]
          ? mapObj[label] + +toy.price / toysPerLable.length
          : +toy.price / toysPerLable.length);
      });
    });
    return mapObj;
  };

  options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: "Prices per toy type",
      },
    },
  };

  map = this.mapChart();

  data = {
    labels: Object.keys(this.map),
    datasets: [
      {
        label: "price",
        data: Object.values(this.map),
        backgroundColor: "#dff0ea",
      },
    ],
  };
  render() {
    return (
      <React.Fragment>
        <h2 className="margin-top">Prices per toy type</h2>
        <Link to="/"><button>Go back</button></Link>
        <Bar options={this.options} data={this.data} />
      </React.Fragment>
    );
  }
}

function mapStateToProps({ toyModule }) {
  return {
    toys: toyModule.toys,
  };
}
export const ToyChart = connect(mapStateToProps)(_ToyChart);
