import React from "react";
import Select from "react-select";
const options = [
    { value: "On wheels", label: "On wheels" },
    { value: "Art", label: "Art" },
    { value: "Box game", label: "Box game" },
    { value: "Baby", label: "Baby" },
    { value: "Doll", label: "Doll" },
    { value: "Puzzle", label: "Puzzle" },
    { value: "Outdoor", label: "Outdoor" },
  ];
export class ToyFilter extends React.Component {
  state = {
    currFilterBy: {
      inStock: "ALL",
      name: "",
      label: "",
    },
  };

  handleChange = ({ target }) => {
    const field = target.name;
    var value;
    if (target.value === "ALL") value = "ALL";
    else if (target.value === "STOCK") value = target.value === "STOCK";
    else if (target.value === "OUT") value = !target.value === "OUT";
    else value = target.value;
    this.setState(
      (prevState) => ({
        currFilterBy: { ...prevState.currFilterBy, [field]: value },
      }),
      console.log("this.state", this.state)
    );
  };

  handleChangeTxt = ({ target }) => {
    const field = target.name;
    var value = target.value;
    this.setState((prevState) => ({
      currFilterBy: { ...prevState.currFilterBy, [field]: value },
    }));
    this.props.onSetFilter(this.state.currFilterBy);
  };

  onHandleChangeMulti = (options) => {
    console.log("options", options);
    this.setState((prevState) => ({
        currFilterBy: { ...prevState.currFilterBy, label: [...options.map(option=>option.value)] },
    }));
  };

  onSubmitFilter = (ev) => {
    ev.preventDefault();
    console.log("filter......");
    const { currFilterBy } = this.state;
    this.props.onSetFilter(currFilterBy);
  };

  render() {
    return (
      <section className="toy-filter">
            <h2 className="margin-top">Filter</h2>
        <form onSubmit={this.onSubmitFilter}>
          <div className="filter-container">
            <div>
              <input
                type="text"
                name="name"
                placeholder="Serch by text"
                onChange={this.handleChangeTxt}
              ></input>
            </div>
            <div>
              <select onChange={this.handleChange} name="inStock">
                <option value="ALL">All Stocks</option>
                <option value="STOCK">In Stock</option>
                <option value="OUT">Out of Stock</option>
              </select>
            </div>
            <div>
            <Select 
                // defaultValue={[colourOptions[2], colourOptions[3]]}
                isMulti
                name="label"
                options={options}
                className="basic-multi-select"
                classNamePrefix="select"
                onChange={this.onHandleChangeMulti}
              />
            </div>
            <button>Filter</button>
          </div>
        </form>
      </section>
    );
  }
}
