export const storageService = {
  query,
  get,
  post,
  put,
  remove,
};
function query(entityType, currFilterBy=null, delay = 100) {
    
    let entities = JSON.parse(localStorage.getItem(entityType)) || []
    if(currFilterBy)console.log('currFilterBy.label', currFilterBy.label);
    if (!currFilterBy) {
        currFilterBy = { inStock: 'ALL', name: '' }
    }
     if (currFilterBy.inStock !== 'ALL') {
        entities = entities.filter((entity) => {
       
            return entity.inStock === currFilterBy.inStock
        })
    }

    if (currFilterBy.name !== '') {
        entities = entities.filter((entity) => {
            return entity.name.includes(currFilterBy.name)
        })
    }
    if (currFilterBy.label) {

 entities = entities.filter(entity=>{
     console.log('entity.label', entity.label , 'currFilterBy.label', currFilterBy.label);
     return entity.label.includes(...currFilterBy.label)
 })
 
    }
    return new Promise((resolve, reject) => {
        setTimeout(() => {
            console.log('entities', entities);
            resolve(entities)
        }, delay)
    })
    // return Promise.resolve(entities)
}
    


function get(entityType, entityId) {
  return query(entityType).then((entities) =>
    entities.find((entity) => entity._id === entityId)
  );
}

function post(entityType, newEntity) {
  newEntity._id = _makeId();
  newEntity.createdAt = Date.now();
  return query(entityType).then((entities) => {
    entities.push(newEntity);
    _save(entityType, entities);
    return newEntity;
  });
}

function put(entityType, updatedEntity) {
  return query(entityType).then((entities) => {
    const idx = entities.findIndex(
      (entity) => entity._id === updatedEntity._id
    );
    entities.splice(idx, 1, updatedEntity);
    _save(entityType, entities);
    return updatedEntity;
  });
}

function remove(entityType, entityId) {
  return query(entityType).then((entities) => {
    const idx = entities.findIndex((entity) => entity._id === entityId);
    entities.splice(idx, 1);
    _save(entityType, entities);
  });
}

function _save(entityType, entities) {
  console.log("entityType FROM SAVE!", entityType);
  localStorage.setItem(entityType, JSON.stringify(entities));
}

function _makeId(length = 5) {
  var text = "";
  var possible =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  for (var i = 0; i < length; i++) {
    text += possible.charAt(Math.floor(Math.random() * possible.length));
  }
  return text;
}
