import { connect } from "react-redux";
import React from "react";
import { toyService } from "../services/toyService";
import toyImg from "../assets/img/toy3.JPG";
import {Reviews} from '../cmps/Reviews.jsx'
class _ToyDetails extends React.Component {
  state = {
    toy: null,
  };

  componentDidMount() {
    const { toyId } = this.props.match.params;
    toyService.getById(toyId).then((toy) => {
      this.setState({ toy });
    });
  }

  onBack = () => {
    this.props.history.push("/");
  };

  render() {
    const { toy } = this.state;
    if (!toy) return "No such toy with this Id";
    return (
      <section className="main-container toy-details">
        <div>
          <h1 className="margin-top">{toy.name} Details</h1>
          <div>
            <button onClick={this.onBack}>Back</button>
          </div>
          <h3>
            label:{" "}
            {toy.label.map((l, idx) => {
              return <span key={idx}>{l} </span>;
            })}
          </h3>
          <h3>Price: {toy.price}</h3>
          <p className={toy.inStock ? "green" : "red"}>
            {toy.inStock ? "In Stock" : "Out of Stock"}
          </p>

          <hr></hr>
          <p>
            Wonder Woman don't you ever forget You're divine and he's about to
            regret <br></br>He's a bucka-mhm-buckbuckbuck-mhm boy
            Bucka-mhm-buckbuckbuck I'm not your bucka-mhm-buck-mhm-buck-mhm{" "}
            <br></br> I'm not your toy (not your toy) You stupid boy (stupid
            boy) I'll take you down now,<br></br> make you watch me Dancing with
            my dolls on the motha-bucka beat Not your toy (cululoo, cululoo)
            (Cululoo, cululoo)
          </p>
          <div className="img-con">
            <img src={toyImg}></img>
          </div>
        </div>
          <Reviews toy={toy}/>
      </section>
    );
  }
}

function mapStateToProps({ toyModule }) {
  return {
    toys: toyModule.toys,
  };
}

export const ToyDetails = connect(mapStateToProps)(_ToyDetails);
