import { storageService } from "./async-storage.service.js";
import { httpService } from "./http.service";
import Axios from "axios";
var axios = Axios.create({
  withCredentials: true,
});

const STORAGE_KEY = "toy";

export const toyService = {
  query,
  getById,
  save,
  remove,
};

async function query(currFilterBy = null) {
  if (currFilterBy) {
    console.log('currFilterBy:', currFilterBy);
    
    // const labelsQueryStr = currFilterBy.label
    //   ? "label=" + currFilterBy.label.join(",")
    //   : "";
    try {
      const toys = await httpService.get(`toy`, currFilterBy);
      return toys;
    } catch (err) {
      console.log("err", err);
    }
  } else {
    const toys = await httpService.get(`toy`);
    return toys;
  }
  // storageService.query(STORAGE_KEY, currFilterBy);
}

async function getById(toyId) {
  try {
    const toy = await httpService.get(`toy/${toyId}`);
    return toy;
  } catch (err) {
    console.log(err.response.status);
  }
  return storageService.get(STORAGE_KEY, toyId);
}

async function remove(toyId) {
  try {
    await httpService.delete(`toy/${toyId}`);
  } catch (err) {
    console.log(err, "err");
    throw new Error(err);
  }

  // return Promise.reject('Not now!');
  // return storageService.remove(STORAGE_KEY, toyId);
}

async function save(toy) {
  if (toy._id) {
    toy.createdAt = Date.now();
    try {
      const savedToy = await httpService.put(`toy/${toy._id}`, toy);
      return savedToy;
    } catch (err) {
      console.log(err, "err");
      throw new Error(err);
    }
    // return storageService.put(STORAGE_KEY, toy);
  } else {
    try {
      const savedToy = await httpService.post("toy", toy);
      return savedToy;
    } catch (err) {
      console.log(err, "err");
    }
    // return storageService.post(STORAGE_KEY, toy);
  }
}
