import { connect } from "react-redux";
import React from "react";
import { signup, logout } from "../store/user.action";
import {  log} from "../store/user.action";
import { userService } from "../services/userService.js";
import { Login } from "../cmps/Login.jsx";

class _HomePage extends React.Component {
  state = { user: null, isSignup: false };

  componentDidMount() {
    this.setState(
      { user: this.props.user },
      console.log("this.state.user", this.state.user)
    );
  }

  onLogin = (credentials) =>{
    this.props.log(credentials);
  }


  onSignup = (credentials) => {
    this.props.signup(credentials);
    // return async (dispatch) => {
    //   try {
    //     const user = await userService.signup(credentials);
    //     dispatch({ type: "SET_USER", user });
    //   } catch (err) {
    //     console.log("err onSignup home page", err);
    //   }
    // };
  };

  onLogout = () => {
      console.log('logout');
    this.props.logout();
    // return async (dispatch) => {
    //   try {
    //     await userService.logout();
    //     dispatch({ type: "SET_USER", user: null });
    //   } catch (err) {
    //     console.log("err onLogout home page", err);
    //   }
    // };
  };

  render() {
    const { isSignup } = this.state;
    return (
      <Login
        onLogin={this.onLogin}
        onSignup={this.onSignup}
        onLogout={this.onLogout}
        toggleSignup={this.toggleSignup}
        isSignup={isSignup}
        user= {this.props.user}
      />
    );
  }
}
function mapStateToProps({ userModule }) {
  return {
    user: userModule.user,
    //   currFilterBy: toyModule.currFilterBy
  };
}
const mapDispatchToProps = {
    log,
  signup,
  logout,
  
};

export const HomePage = connect(mapStateToProps, mapDispatchToProps)(_HomePage);
