import { Component } from "react";
import { connect } from "react-redux";
import React from 'react'
import { loadToys, removeToy, setFilter, updateFilter } from "../store/toy.action";
import { ToyAdd } from "../cmps/ToyAdd";
import { ToyList } from "../cmps/ToyList";
import { ToyFilter } from "../cmps/ToyFilter";


class _ToyApp extends React.Component {
  state = {};
  componentDidMount() {
   this.props.loadToys()
   console.log('this.props.toys', this.props.toys);
  }

  
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.currFilterBy !== this.props.currFilterBy) {
      const { currFilterBy } = this.props;
      return this.props.updateFilter(currFilterBy)
    }
  }

  onRemoveToy = (todoId) => {
      this.props.removeToy(todoId)
  }

  
  onSetFilter = (currFilterBy) => {
    this.props.setFilter(currFilterBy);
  }

  render() {
    return (
      <section className="main-container">
        <div className="control-container">
        <ToyAdd />
        <ToyFilter onSetFilter={this.onSetFilter}/>
        </div>
        <section className="toy-list">
        <ToyList toys={this.props.toys} onRemoveToy={this.onRemoveToy}/>
        </section>
        <footer>
          <h3>All rights reserved to miss toy</h3>
        </footer>
      </section>
    );
  }
}

function mapStateToProps({toyModule}) {
  return {
    toys: toyModule.toys,
    currFilterBy: toyModule.currFilterBy
  };
}

const mapDispatchToProps = {
  loadToys,
  removeToy,
  setFilter,
  updateFilter
};

export const ToyApp = connect(mapStateToProps, mapDispatchToProps)(_ToyApp);
