import React from 'react'
import {Link} from 'react-router-dom'

export function ToyPreview({ toy, onRemoveToy }) {
if(!toy) return 'no toys to show'
  return (
    <div
      className="toy-container">
      <div className="toy-preview-details">
       <h1>{toy.name}</h1> 
        { toy.label.map((l, idx)=>{
            return <h3 key={idx} >{l} </h3>
        })}
        <p>{toy.price}</p>
        <p className={toy.inStock ? "green" : "red"}>{toy.inStock ? "In Stock" : 'Out of Stock'}</p>
      </div>
<div className="btn-control-toy">
      <Link to={`/edit/${toy._id}`}>
        <button>Edit</button>
      </Link>
      <Link to={`/details/${toy._id}`}>
        <button>More Detailes</button>
      </Link>
      <button onClick={() => onRemoveToy(toy._id)}>Delete</button>
    </div>
</div>
  );
}


