import { useState, useEffect } from "react";
import { connect } from "react-redux";
import {
  loadReviews,
  addReview,
  removeReview,
} from "../store/review.actions.js";
import { loadUsers } from "../store/user.action";

function _Reviews({
  toy,
  reviews,
  loggedInUser,
  loadReviews,
  addReview,
  users,
}) {
  const [reviewToEdit, setReviewToEdit] = useState({ txt: "", toyId: toy._id });

  useEffect(() => {
    loadReviews(toy._id);
    loadUsers();
    setReviewToEdit({ txt: "", toyId: toy._id });
    console.log("reviews:", reviews);
  }, []);

  const handleChange = (ev) => {
    const { name, value } = ev.target;
    setReviewToEdit({ ...reviewToEdit, [name]: value });
  };

  const onAddReview = async (ev) => {
    ev.preventDefault();
    if (!reviewToEdit.txt) return alert("All fields are required");
    console.log("reviewToEdit:", reviewToEdit);

    await addReview(reviewToEdit);
    setReviewToEdit({ txt: "", toyId: toy._id });
  };

  return (
    <section>
      {loggedInUser && (
        <section>
          <h2>Add Reviews:</h2>
          <form onSubmit={onAddReview}>
            <textarea
              name="txt"
              onChange={handleChange}
              value={reviewToEdit.txt}
            ></textarea>
            <div>
              <button>Add</button>
            </div>
          </form>
        </section>
      )}

      <section>
        {reviews &&
          reviews.map((review, idx) => {
            console.log("review,", review);
            return (
              <div key={idx} className="review-container">
                {/* <h3> Name: {review.aboutUserId}</h3> */}
                <h3> Review: {review?.txt}</h3>
                <h3> User: {review.byUser?.username}</h3>
              </div>
            );
          })}
      </section>
    </section>
  );
}

const mapStateToProps = (state) => {
  return {
    reviews: state.reviewModule.reviews,
    users: state.userModule.users,
    loggedInUser: state.userModule.user,
  };
};
const mapDispatchToProps = {
  loadReviews,
  loadUsers,
  addReview,
  // removeReview
};

export const Reviews = connect(mapStateToProps, mapDispatchToProps)(_Reviews);
